import { Component } from "react";
import { withRouter } from "react-router";
import globalStyles from '../../common/style';
import { token, user } from '../../request';
class RedirectScreen extends Component {
    state = {
        email: "",
        password: ""
    }
    constructor(props) {
        super(props)
        if (token !== null && user !== null) {
            setTimeout(function () {
                window.location.href = "/reports";
            }, 500)
        } else {
            setTimeout(function () {
                window.location.href = "https://www.expercepte.com";
            }, 500)
        }
    }
    componentDidMount() {

    }
    randomColorGenerator() {
        const colors = [globalStyles.bartyGreen, globalStyles.bartyDark, globalStyles.red, globalStyles.yelOrange];
        const randomIndex = Math.floor(Math.random() * colors.length);
        return colors[randomIndex];
    }
    render() {
        return (
            <div style={{
                position: "absolute",
                width: "100%",
                height: "100%",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                backgroundColor: globalStyles.white
            }}>
                <div>
                    <div class="spinner-border" style={{
                        width: "5rem",
                        height: "5rem",
                        borderWidth: "0.5rem"
                    }} role="status">
                        <span class="sr-only">Loading...</span>
                    </div>
                </div>
            </div>
        )
    }
}
export default withRouter(RedirectScreen);