    updateCosmeticsFromCosmeticNotes() {
        let cosmetics = this.stateInitial.cosmetics;
        let cosmeticsNotes = Object.create(this.state.cosmeticsNotes);
        ["front", "back", "left", "right", "top", "bottom"].forEach((type, i) => {
            cosmeticsNotes?.[type].forEach((item, index) => {
                (item?.codes || []).forEach((code, index) => {
                    const arr = Array.from(cosmetics?.[type]?.[code]?.imagesArr || []);
                    arr.push(item?.images)
                    cosmetics = {
                        ...cosmetics,
                        [type]: {
                            ...cosmetics?.[type],
                            [code]: {
                                ...cosmetics?.[type]?.[code],
                                problems: (cosmetics?.[type]?.[code]?.problems || []).concat(item?.problem),
                                images: (cosmetics?.[type]?.[code]?.images || []).concat(...item?.images),
                                imagesArr: arr,
                                problemTypes: (cosmetics?.[type]?.[code]?.problemTypes || []).concat(item?.problemType)
                            }
                        }
                    }
                    console.log(cosmetics)
                })
            })
        })
        this.setState({
            cosmetics
        }, () => {
            this.saveLatestStateToLocalStorage()
        })
    }
    documentListeners() {
        const context = this;
        window.addEventListener('beforeunload', this.saveLatestStateToLocalStorage.bind(this));
        document.addEventListener("keydown", function (event) {
            if (event.key === "Control" || event.key === "Alt" || event.metaKey) {
                context.toggleMultipleSelectionMode(true)
            }
        })
        document.addEventListener("keyup", function (event) {
            if (event.key === "Control" || event.key === "Alt" || event.metaKey) {
                context.toggleMultipleSelectionMode(false)
            }
        })
    }
