    multipleSelectionHasData() {
        const initialData = this.multipleResetData();
        const currentData = this.state.multipleCheckSum;
        if (_.isEqual(initialData, currentData)) {
            return false;
        } else {
            return true
        }
    }
    toggleMultipleSelectionMode(bool) {
        if (this.state.multipleSelectionMode !== bool) {
            this.setState(state => ({
                multipleSelectionMode: bool,
                multiple: this.multipleResetData(),
                multipleCheckSum: this.state.multiple
            }), () => {
                console.log(this.state.multipleSelectionMode)
                if (this.state.multipleSelectionMode === false && this.multipleSelectionHasData()) {
                    console.log(JSON.stringify(this.state.multipleCheckSum))
                    this._multipleModalOpener.click()
                }
            })
        }
    }
    randomIdGenerator(length = 6) {
        let chars = "ABCDEFHIJKLMNOPRSTUVYZabcdefghijklmnoprstuvyz0123456789";
        let res = "";
        for (let i = 0; i < length; i++) {
            res += chars.charAt(Math.floor(Math.random() * chars.length));
        }
        return res;
    }
