    /* COSMETICS NOTES MULTIPLE CELL INPUT MODAL METHODS ENDS */
    renderMultipleCellModalOpener() {
        return <button
            type="button"
            class="btn btn-primary"
            style={{ display: "none" }}
            ref={(modalOpener) => {
                this._multipleModalOpener = modalOpener
            }}
            data-bs-toggle="modal"
            data-bs-backdrop='static'
            data-bs-keyboard='false'
            data-bs-target="#cosmeticsMultipleInputModal">
            Open modal for @mdo
        </button>
    }
    onSelectChangeMultipleCellModal = (e) => {
        this.setState({
            multipleCellModalSelectValue: e.target.value
        })
    }
    renderOptionsMultipleCellModal() {
        const value = this.state.cosmeticsNotes[this.state.type]?.find((element, index) => {
            if (element?.id === this.state.updateCellsModalId) {
                return true
            }
            return false;
        })
        return (
            this.cosmeticTypes.map((item, index) => {
                return <option
                    value={item}
                    selected={item === value?.problemType}>
                    {item}
                </option>
            })
        )
    }

    closeWithoutSaveMultipleCellModal = (e) => {
        this.setState({
            multipleCellModalSelectValue: this.cosmeticTypes[0],
            multipleCellModalTextValue: "",
            multipleCellModalImages: [],
            multipleCellDeleteImage: [],
            multipleCheckSum: this.multipleResetData()
        }, () => {
            this._multipleCellModalFileInput.value = null
        })
    }
    closeAndSaveMultipleCellModal = (e) => {
        const arr = Object.keys(this.state.multipleCheckSum?.[this.state?.type]).map((code, index) => {
            return {
                code,
                problem: this.state.multipleCellModalTextValue,
                problemTypes: (this.state.cosmetics[this.state.type]?.[code]?.problemTypes || []).concat(this.state.multipleCellModalSelectValue)
            }
        });
        const obj = {

        }
        const codes = []
        arr.forEach((item, index) => {
            codes.push(item?.code)
            obj[item?.code] = {
                ...item
            }
            console.log(obj);
        })
        this.setState(state => ({
            cosmetics: {
                ...state?.cosmetics,
                [this.state.type]: {
                    ...state.cosmetics[this.state.type],
                    ...obj
                }
            },
            cosmeticsNotes: {
                ...state?.cosmeticsNotes,
                [this.state.type]: [
                    ...state?.cosmeticsNotes[this.state.type],
                    {
                        id: this.randomIdGenerator(10),
                        sort: this.state.cosmeticsNotes[this.state.type].length,
                        codes: codes.sort(),
                        problemType: this.state.multipleCellModalSelectValue,
                        problemTypes: [this.state.multipleCellModalSelectValue],
                        problem: this.state.multipleCellModalTextValue,
                        images: this.state.multipleCellModalImages
                    }
                ]

            },
            multipleCellModalSelectValue: this.cosmeticTypes[0],
            multipleCellModalTextValue: "",
            multipleCellModalImages: [],
            multipleCellDeleteImage: []
        }), () => {
            this._multipleCellModalSelect.value = this.cosmeticTypes[0]
            this._multipleCellModalTextArea.value = this.state.multipleCellModalTextValue
            this._multipleCellModalFileInput.value = null
            this.updateCosmeticsFromCosmeticNotes()
        })
    }

    fileUploadHandlerMultipleCellModal = (e) => {
        this.setState({
            multipleCellModalImagesLength: e.target.files.length
        }, () => {
            const context = this;
            [].forEach.call(e.target.files, (file, index) => {
                aws.s3.uploadFile(file)
                    .then(fileResult => {
                        context.setState(state => ({
                            multipleCellModalImagesLength: state.multipleCellModalImagesLength - 1,
                            multipleCellModalImages: state.multipleCellModalImages.concat(`https://report.xprcsv.net/public/${fileResult?.key}`)
                        }))
                    })
                    .catch(err => {
                        console.error(err)
                    })
            })
        })

    }

