    renderGeneralRadioButtons(title, stateId, callback = () => { }) {
        return (
            <div class="row"
                style={{
                    height: "80px",
                    marginTop: "5px",
                    marginBottom: "25px"
                }}>
                <div class="col-lg-5 col-12"
                    style={{
                        margin: "auto"
                    }}>
                    <div style={{
                        textAlign: "left",
                        flex: 1,
                        fontWeight: "bold",
                    }}>
                        {title || ""}
                    </div>
                </div>
                <div class="col-lg-7 col-12" style={{
                    textAlign: "left",
                    height: "100%",
                    alignItems: "center",
                    display: "flex",
                    flex: 1
                }}>
                    <div>
                        <div class="form-check form-check-inline"
                            style={{
                                marginRight: "30px",
                                justifyContent: "center",
                                alignItems: "center",
                                padding: "20px"
                            }}>
                            <input class="form-check-input"
                                type="radio"
                                ref={(positive) => {
                                    this._positiveInputGeneral = positive
                                }}
                                name={stateId}
                                defaultChecked={
                                    this.state.general?.[stateId]
                                        ? this.state.general?.[stateId]
                                        : null
                                }
                                onChange={(e) => {
                                    if (this.state.general[stateId] !== true) {
                                        this.setState((state) => ({
                                            general: {
                                                ...state?.general,
                                                [stateId]: true
                                            }
                                        }), callback);
                                    }
                                }}
                                checked={
                                    this.state.general[stateId] !== undefined
                                    && this.state.general[stateId] !== null
                                    && this.state.general[stateId] !== false
                                }
                            />
                            <label class="form-check-label" style={{
                                fontWeight: "bold"
                            }} for="flexCheckDefault">
                                Var
                            </label>
                        </div>
                        <div class="form-check form-check-inline" style={{
                            marginRight: "30px"
                        }}>
                            <input class="form-check-input"
                                type="radio"
                                ref={(negative) => {
                                    this._negativeInputGeneral = negative
                                }}
                                name={stateId}
                                defaultChecked={
                                    this.state.general?.[stateId] === false
                                        ? true
                                        : null
                                }
                                onChange={(e) => {
                                    if (this.state.general[stateId] !== false) {
                                        this.setState((state) => ({
                                            general: {
                                                ...state?.general,
                                                [stateId]: false
                                            }
                                        }), callback);
                                    }
                                }}
                                checked={
                                    this.state.general[stateId] !== undefined
                                    && this.state.general[stateId] !== null
                                    && this.state.general[stateId] !== true
                                }
                            />
                            <label class="form-check-label" style={{
                                fontWeight: "bold"
                            }} for="flexCheckDefault">
                                Yok
                            </label>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
