import { Storage } from 'aws-amplify';
import { v4 as uuidv4 } from 'uuid';
function makeUID() {
    return uuidv4()
}
function uploadFile(
    file,
    cb = (storageResult) => {

    },
    options = {
        level: "public",
        type: "images",
        key: makeUID()
    }) {
    console.log(file);
    const typeArr = file?.name.split(".");
    const type = typeArr[typeArr.length - 1];
    return new Promise(async (resolve, reject) => {
        try {
            const fileKey = `${options?.type}/${options?.key}.${type}`;
            const storageResult = await Storage.put(fileKey, file, {
                level: options?.level,
                type: file?.type
            })
            cb(storageResult)
            resolve(storageResult);
        } catch (e) {
            reject(false)
        }
    })
}
export default {
    uploadFile
}