import React, { Component } from 'react';
import globalStyles from '../../common/style';
import { Link } from 'react-router-dom';
import { withRouter } from "react-router";
import moment from "moment";
import { token, user } from "../../request";
import Logo from '../../assets/svg/logo1.svg';
class ReportsScreen extends Component {
    state = {
        localStorageKeys: [],
        localStorageData: {},
        newReportID: this.randomNumStringGenerator()
    }
    constructor(props) {
        super(props);
        if (token === null) {
            //return;
            setTimeout(function () {
                window.location.href = "/login";
            }, 1000)
        }
    }

    componentDidMount() {
        this.localStorageHandlerForState()
    }

    randomNumStringGenerator(length = 10) {
        const chars = "0123456789";
        let result = "";
        for (let i = 0; i < length; i++) result += chars.charAt(Math.floor(Math.random() * chars.length));
        return result;
    }
    randomNumberGenerator(length = 8) {
        const nums = "0123456789";
        let result = "";
        for (let i = 0; i < length; i++) result += nums.charAt(Math.floor(Math.random() * nums.length));
        return result;
    }

    localStorageHandlerForState() {
        const items = { ...localStorage };
        let keys = Object.keys(items);
        const allDataKeysSaved = keys.filter((key, i) => {
            if (key.includes("_report_")) {
                return true
            } else {
                return false
            }
        })
        const localStorageData = items;
        const finalData = {};
        allDataKeysSaved.reverse().forEach((item, index) => {
            finalData[item] = JSON.parse(localStorageData[item])
        })
        this.setState({
            localStorageKeys: allDataKeysSaved,
            localStorageData: finalData
        })
    }
    reporterNavbar() {
        return (
            <div className="navbar-reporter" style={{
                position: "absolute",
                top: "0px",
                left: "0px",
                height: "70px",
                width: "100vw",
                zIndex: 999,
                backgroundColor: globalStyles.white
            }}>
                <div className="container-fluid">
                    <div className="row" style={{ height: "100px" }}>
                        <div className="col-4">
                            <div className="d-flex align-items-center justify-content-center" style={{
                                height: "70px"
                            }}>
                                <embed src={Logo} style={{ width: "200px" }} />
                            </div>
                        </div>
                        <div className="col-8">
                            <div className="d-flex align-items-center justify-content-end" style={{
                                height: "70px",
                                padding: "20px 15px",
                            }}>
                                <div className="d-flex align-items-center justify-content-center">
                                    <div style={{
                                        flex: 1,
                                        padding: "0px 20px",
                                        fontFamily: globalStyles.fonts.GilroyBlack,
                                        fontSize: "15px",
                                    }}>{user?.name + " " + user?.surname}</div>
                                    <button className="btn btn-secondary" onClick={() => {
                                        window.location.href = `/reporter/${this.state.newReportID}`
                                    }}
                                        style={{
                                            backgroundColor: globalStyles.bartyGreen,
                                            borderColor: globalStyles.bartyGreen,
                                            color: globalStyles.bartyDark,
                                            fontFamily: globalStyles.fonts.GilroyBlack,
                                            fontSize: "15px",
                                            height: "50px",
                                            padding: "10px 10px",
                                            borderRadius: "12px",
                                            marginRight: "15px"
                                        }}>Yeni Rapor Başlat</button>
                                    <button className="btn btn-danger"
                                        onClick={() => {
                                            localStorage.removeItem("__token");
                                            localStorage.removeItem("__user");
                                            window.location.href = "/login";
                                        }}
                                        style={{
                                            fontFamily: globalStyles.fonts.GilroyBlack,
                                            fontSize: "15px",
                                            height: "50px",
                                            padding: "10px 10px",
                                            borderRadius: "12px",
                                            marginRight: "15px"
                                        }}>Çıkış Yap</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div >

        )
    }
    renderStartReport() {
        return (
            <a role="a" target="__blank" href={`reporter/${this.state.newReportID}`} style={{
                width: '100%',
                textAlign: "center",
                height: "100px",
                margin: "auto",
                marginBottom: "15px",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                fontSize: "25px",
                fontFamily: "'Rubik',sans-serif",
                backgroundColor: globalStyles.bartyGreen,
                color: globalStyles.bartyDark,
                textDecoration: "none"
            }}>
                <i style={{ marginRight: "10px", fontSize: "3rem" }} className="fa fa-plus-circle"></i> Yeni Rapor Başlat
            </a>
        )
    }
    renderReports({ key }) {
        return (
            <div class="col-lg-3 col-md-6 col-sm-12"
                style={{
                    minHeight: "150px",
                    marginTop: "15px"
                }}>
                <div style={{
                    padding: "15px",
                    height: "inherit",
                    width: "100%",
                    height: "auto",
                    backgroundColor: this.state.localStorageData[key]?.completed
                        ? globalStyles.bartyGreen
                        : globalStyles.yelOrange,
                    color: globalStyles.bartyDark
                }}>
                    <div
                        role="button"
                        href={!this.state.localStorageData[key]?.completed
                            ? `/report/${key.replace("_report_", "")}`
                            : `/view/${this.state.localStorageData[key]?._id}`}
                        style={{
                            color: globalStyles.bartyDark
                        }}>
                        <b>Model: {this.state.localStorageData[key]?.general?.model || "Girilmedi"}</b>
                        <br />
                        <b>Marka: {this.state.localStorageData[key]?.general?.brand || "Girilmedi"}</b>
                        <br />
                        <b>Raporun Durumu: {this.state.localStorageData[key]?.completed ? "Tamamlandı" : "Tamamlanmadı"}
                        </b>
                        <br />
                        <b>Başlanma Tarihi: {
                            moment(this.state.localStorageData[key]?.general?.startDate).format("LLL")
                        }
                        </b>
                        <div style={{
                            display: "flex",
                            fontFamily: globalStyles.fonts.GilroyBlack,
                            marginTop: "20px"
                        }}>
                            <a className="btn btn-primary" style={{ flex: 1, margin: "0px 2.5px 0px 5px" }} href={`/report/${key.replace("_report_", "")}`}>Raporun Verisine Ulaş</a>
                            {this.state.localStorageData[key]?.completed && <a style={{ flex: 1, margin: "0px 5px 0px 2.5px" }} className="btn btn-secondary" href={`/rapor/${this.state.localStorageData[key]?._id}`}>Raporu Gör</a>}
                        </div>
                        <div style={{
                            display: "flex",
                            fontFamily: globalStyles.fonts.GilroyBlack,
                            marginTop: "5px"
                        }}>
                            <a className="btn btn-danger"
                                onClick={() => {
                                    localStorage.removeItem(key);
                                    this.localStorageHandlerForState()
                                }}
                                style={{ flex: 1, margin: "0px 2.5px 0px 2.5px" }}><i className="fa fa-trash"></i> Rapor Verisini Kaldır</a>
                        </div>
                    </div>
                </div>
            </div >
        )
    }
    render() {
        if (token === null) {
            return <div style={{
                position: "absolute",
                width: "100%",
                height: "100%",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                backgroundColor: globalStyles.white
            }}>
                <div>
                    <div class="spinner-border" style={{
                        width: "5rem",
                        height: "5rem",
                        borderWidth: "0.5rem"
                    }} role="status">
                        <span class="sr-only">Loading...</span>
                    </div>
                </div>
            </div>
        }
        return (
            <div class="container-fluid" style={{
                backgroundColor: globalStyles.white,
                minHeight: '100vh'
            }}>
                <style>{`
.navbar-reporter{
box-shadow: 1px 12px 64px -22px rgba(0,0,0,0.38);
-webkit-box-shadow: 1px 12px 64px -22px rgba(0,0,0,0.38);
-moz-box-shadow: 1px 12px 64px -22px rgba(0,0,0,0.38);
}
`}</style>
                <div class="row">
                    {this.reporterNavbar()}
                </div>
                <div class="row" style={{ marginTop: "75px" }}>
                    {this.state.localStorageKeys.length === 0 && <div style={{ textAlign: "center", width: '100%', height: "100%", display: "flex", alignItems: "center", justifyContent: "center" }}> Burada Henüz Bir Rapor Bulunmuyor</div>}
                    {this.state.localStorageKeys.sort(function (a, b) {
                        var dateA = new Date(a?.general?.startDate).getTime();
                        var dateB = new Date(a?.general?.startDate).getTime();
                        return dateA < dateB ? 1 : -1; // ? -1 : 1 for ascending/increasing order
                    }).map((item, index) => {
                        return this.renderReports({
                            key: item
                        })
                    })}
                </div>
            </div>
        )
    }
}
export default withRouter(ReportsScreen)