import { useState } from 'react'
import aws from "../../utils/aws";

function TestAmplifyScreen() {
    const [file, setFile] = useState();
    const [uploaded, setUploaded] = useState(false);
    const [uploadedPhoto, setUploadedPhoto] = useState({ key: "" });
    return (
        <div className="App">
            <input type="file" onChange={(e) => setFile(e.target.files[0])} />
            <button onClick={async () => {
                //const fileResult = await aws.s3.uploadFile(file);
                aws.s3.uploadFile(file).then(fileResult => {
                    setUploadedPhoto(fileResult)
                    setUploaded(true)
                    console.log(fileResult)
                }).catch(err => {
                    console.error(err)
                })
                /* uploadFile(file, (result) => {
                    setUploadedPhoto(result)
                    setUploaded(true)
                    console.log(result);
                }) */
                // Insert predictions code here later

            }}>Upload and check if there's a dog!</button>

            <div>
                {uploaded
                    ? <div>Your image is uploaded!: <a href={`https://report.xprcsv.net/public/${uploadedPhoto?.key}`}>{`https://report.xprcsv.net/public/${uploadedPhoto?.key}`}</a></div>
                    : <div>Upload a photo to get started</div>}
            </div>
        </div>
    );
}

export default TestAmplifyScreen;