import { Storage } from 'aws-amplify';
function removeFile(
    key,
    options = {
        level: "public",
    }) {
    return new Promise(async (resolve, reject) => {
        try {
            const fileKey = `${key}`;
            console.log(key)
            const storageResult = await Storage.remove(fileKey, {
                level: "public"
            })
            resolve(storageResult);
        } catch (e) {
            reject(false)
        }
    })
}
export default {
    removeFile
}