import { Component } from "react";
import { withRouter } from "react-router";
import globalStyles from '../../common/style';
import Logo from '../../assets/png/logo1.png';
import request from '../../request';
import { token, user } from '../../request';
class LoginScreen extends Component {
    state = {
        email: "",
        password: ""
    }
    constructor(props) {
        super(props)
        if (token !== null && user !== null) {
            setTimeout(function () {
                window.location.href = "/reports";
            }, 1000)
        }
        this.login = this.login.bind(this)
    }
    componentDidMount() {

    }
    login() {
        request.post("/login", {
            email: this.state.email,
            password: this.state.password
        }).then((res) => {
            localStorage.setItem("__token", res.data.token);
            localStorage.setItem("__user", JSON.stringify(res.data.user))
            window.location.href = "/reports"
        }).catch(err => {
            alert("Yanlış Giriş Bilgileri")
        })
    }
    randomColorGenerator() {
        const colors = [globalStyles.bartyGreen, globalStyles.bartyDark, globalStyles.red, globalStyles.yelOrange];
        const randomIndex = Math.floor(Math.random() * colors.length);
        return colors[randomIndex];
    }
    render() {
        if (token !== null && user !== null) {
            return <div style={{
                position: "absolute",
                width: "100%",
                height: "100%",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                backgroundColor: globalStyles.white
            }}>
                <div>
                    <div class="spinner-border" style={{
                        width: "5rem",
                        height: "5rem",
                        borderWidth: "0.5rem"
                    }} role="status">
                        <span class="sr-only">Loading...</span>
                    </div>
                </div>
            </div>
        }
        return (
            <div className="d-flex text-center justify-content-center align-items-center position-fixed"
                style={{
                    left: "50%",
                    top: "50%",
                    transform: "translate(-50%,-50%)",
                }}>
                <style>{`body{background-color:${this.randomColorGenerator()} !important;}`}</style>
                <div style={{
                    flex: 1,
                    backgroundColor: globalStyles.white,
                    padding: "60px 40px",
                    width: "380",
                    maxWidth: "380px",
                    fontFamily: globalStyles.fonts.GilroyBlack,
                    letterSpacing: "0.1px"
                }}>
                    <div className="form-login" style={{ width: "300px" }}>
                        <h1 className="h3 mb-3 font-weight-normal" style={{ marginBottom: "20px", width: "100%", textAlign: "left" }}><embed src={Logo} width="280px" /></h1>
                        <label for="inputEmail" style={{
                            color: globalStyles.bartyDark,
                            textAlign: "left",
                            width: "100%",
                            marginTop: "5px"
                        }} >E-posta adresi:</label>
                        <input type="email" onChange={(e) => {
                            this.setState({
                                email: e.target.value
                            })
                        }} style={{
                            fontFamily: globalStyles.fonts.GilroyMedium
                        }} id="inputEmail" className="form-control mb-2" placeholder="Email'inizi Giriniz" required="" autofocus="" />
                        <label for="inputPassword" style={{
                            color: globalStyles.bartyDark, textAlign: "left", width: "100%", marginTop: "15px"
                        }} >Şifre</label>
                        <input type="password" onChange={(e) => {
                            this.setState({
                                password: e.target.value
                            })
                        }} style={{
                            fontFamily: globalStyles.fonts.GilroyMedium
                        }} id="inputPassword" className="form-control mb-2" placeholder="Şifrenizi Giriniz" required="" />
                        <br />
                        <button className="btn btn-primary" onClick={this.login} style={{
                            width: "100%",
                            fontFamily: globalStyles.fonts.GilroyBlack,
                            fontSize: "19px"
                        }}>
                            Giriş Yap
                        </button>
                    </div>
                </div>
            </div>
        )
    }
}
export default withRouter(LoginScreen);